<template>
  <div class="cake">
    <div class="cake-view">
      <div class="button-front" @click="addLayers()" v-if="list_cake.length <= 0"><span>Zaczynamy?</span></div>
      <!-- * Cake body beginning-->
      <div class="cake-box">
        <!--Animation when adding layers (fade)-->
        <!--<transition-group name="fade" tag="div">-->
        <div  class="layer" :style="{height: item.height*16 +'px'}" v-for="item in list_cake"> <!--:key="item.id"-->
          <div class="circle-top" :class="item.option.layers"></div>
          <div class="mass-center" :class="item.option.layers" :data-inf="item.option.layers"></div>
          <div class="circle-bottom" :class="item.option.layers"></div>
        </div>

        <!--</transition-group>-->
        <div class="plate"></div>
      </div>
      <!-- * Cake body end-->
    </div>

    <div class="cake-options">
      <transition name="fade">
        <div class="cake-options__info" v-if="list_cake.length === 0">
          <div CLASS="cake-options__info--header">
            <h3><strong>Typ ciasta i cena:</strong></h3>
            <button class="btn button-options" @click="addLayers()">Zaczynamy!</button>
          </div>

          <hr>
          <ol>
            <li v-for="item in options">
              <span class="info-default">Nazwa:</span>
              {{item.layers | firstLettersUp}}
              <span class="info-default">cena:</span>
              {{ item.price}} <span class="info-default">zł.</span>
            </li>
          </ol>
          <hr>
          <span class="info-default">
            Cena podana za 1cm. ciasta, i będzie się zmieniać w zależności od gróbości!<br>
            Przekrój ciasta jest ustawiona domyślnie i bez możliwości modyfikacji.<br>
            Domyślnie warstwa jest ustawiona na "Fruit-cream", po dodaniu warstwy jest
            możliwość modyfikacji i wyboru włąsnych parametrów.
          </span>
        </div>
      </transition>
      <transition name="fade">

        <div class="cake-options__table" v-if="list_cake.length > 0">
          <div class="cake-options__header">
            <h2>Razem do zapłaty:</h2> <span style="display: inline-block" class="price-sum">{{sumPrice}} zł.</span>
            <button class= "btn addLayers-button" @click="addLayers()">Dodaj warstwę</button>
            <button class="btn reset-button" @click="deleteAllItems()">Usuń wszystko</button>
          </div>
          <table class="table-options">
            <thead>
            <tr>
              <th>Index</th>
              <th>Rodzaj ciasta</th>
              <th>Cena × 1cm.</th>
              <th>Grubość</th>
              <th>Suma</th>
              <th>Usuń</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item, index) in list_cake" :class="item.option.layers">
              <td>{{index+1}}</td>
              <td>
                <multiselect v-model="item.option"  :options="options" :searchable="false" track-by="layers" :allow-empty="false"   label="layers" :show-labels="false" placeholder="Pick a value"></multiselect>
              </td>
              <td>{{item.option.price}} zł.</td>
              <td>
                <div class="option-min-add">
                <span  @click="height_min(index)" class="option-min">
                </span>
                  <span class="option-min-add">{{item.height}} cm.</span>
                  <span @click="height_plus(index)" class="option-add">
                </span>
                </div>
              </td>
              <td>{{calculate(index)}} zł.</td>
              <td><span class="button-delete" @click="deleteItem(index)"></span></td>
            </tr>
            </tbody>
          </table>
        </div>
      </transition>
    </div>

    <transition name="error">
      <div class="message-error" v-show="message">
        <span>{{message_error}}</span>
      </div>
    </transition>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        myValue: null,
        message: null,
        list_cake: [],
        id: 0,
        options: [
          {layers: 'yeast', price: 2.7},
          {layers: 'butter', price: 3},
          {layers: 'chocolate', price: 6},
          {layers: 'biscuit', price: 4},
          {layers: 'fruit', price: 4.99},
          {layers: 'cream', price: 2.5}
        ],

      }
    },
    filters: {
      firstLettersUp (value) {
        value = value.toString()
        return value.charAt(0).toUpperCase() + value.slice(1)
      }
    },
    methods: {
      addLayers(){
        if (this.list_cake.length <= 5 ){
          let defaultLayers = {
            id: this.id,
            height: 1,
            priceSum: 0,
            option: {
              layers: 'fruit',
              price: 4.99
            }
          };
          this.list_cake.unshift(defaultLayers);
          this.id++;
        } else {
          this.message = 'Nie wolno dodać więcej niż 6 warstw!';
        }

      },
      height_plus: function(index){
        let maxheight;
        maxheight = this.list_cake[index].height;
        if (maxheight >= 5) {
          this.message = "Maksymalna grubość warstwy 5cm!";
          // setTimeout(() => this.message = null, 3000);
        }
        else this.list_cake[index].height += 1;
      },
      height_min: function(index){
        if (this.list_cake[index].height <= 1){
          this.message = 'Minimalna gruboś warstwy wynosi 1cm!';
          // setTimeout(() => this.message = null, 3000);
        }else this.list_cake[index].height -= 1;
      },
      deleteAllItems(){
        this.list_cake.splice(0,this.list_cake.length);
      },
      deleteItem(index) {
        this.list_cake.splice(index,1);
      },
      calculate(index) {
        let price;
        price = this.list_cake[index].option.price * this.list_cake[index].height;
        this.list_cake[index].priceSum = price;
        return price.toFixed(2);
      }
    },
    computed: {
      sumPrice() {
        let i = 0;
        let sum = 0;
        for(i; i<this.list_cake.length; i++) {
          sum += this.list_cake[i].priceSum;
        }
        return sum.toFixed(2);
      },
      message_error() {
        let msg = this.message;
        setTimeout(() => this.message = null, 3000);
        return msg;

      }

    }

  }
  // this.list_cake.slice().reverse();
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css">

</style>

<style lang="scss" scoped>
  * {
    box-sizing: border-box;

  }
  //variable color
  $greenButtonAdd: green;
  // layers
  $biscuit: #F5F5DC;
  $butter: #92867c;
  $yeast: #F0FFFF;
  $cream: #FFF8DC;
  $chocolate: #8B4513;
  $fruit: #FA8072;
  $butter-black: #6d6158;
  $fruit-light: rgba(250, 128, 114, 0.46);

  @keyframes scale_puls {
    0%,60%{transform: scale(1);}
    30%,100%{transform: scale(1.1);}
  }
  @keyframes pulsation {
    0% {
      transform: scale(1, 1);
    }
    50% {
      opacity: 0.3;
    }
    100% {
      transform: scale(1.5);
      opacity: 0;
    }
  }
  @keyframes rotate {
    0% {
      transform: rotate(360deg);
    }
    20%,30%,40%,50%,60%,70%,80% {
      transform: rotate(0);
    }

    100% {
      transform: rotate(360deg);

    }
  }

  .cake {
    display: flex;
    height: 100vh;

    .message-error{
      position: absolute;
      height: 2rem;
      text-align: center;
      width: 20rem;
      padding: .5rem 1rem;
      background: chocolate;
      left: 50%;
      transform: translateX(-50%);
      bottom: 0rem;
      z-index: 5;


    }
    // transition to error message
    .error-enter-active, .error-leave-active {
      transition: opacity .5s;
    }
    .error-enter, .error-leave-to /* .fade-leave-active below version 2.1.8 */ {
      opacity: 0;
    }
    // transition to cake-options__info and  cake-options__table
    .fade-enter-active {
      transition: all 1s ease;
    }
    .fade-leave-active {
      transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .fade-enter, .fade-leave-to {
      /* .slide-fade-leave-active below version 2.1.8 */
      transform: translateX(10px);
      opacity: 0;
    }


     // class to color layers cake and table tr
    .biscuit {background-color: $biscuit;}
    .butter {background-color: $butter;}
    .yeast {background-color: $yeast;}
    .cream {background-color: $cream;}
    .chocolate {background-color: $chocolate;color: white!important;}
    .fruit {background-color: $fruit;}

    .info-default {
      color: rgba(78, 78, 78, 0.79);
      font-size: .8rem;
    }
    //default
    .btn {
      padding: .4rem .4rem;
      outline: none;
      border: none;
      cursor: pointer;
    }

    .cake-view {
      width: 40%;
      position: relative;
      display: flex;
      align-items: flex-end;
      padding-bottom: 2rem;

      .cake-view__header {
        position: absolute;
        top: 0;
        left: 0;
        text-align: center;
        width: 100%;
      }
      .button-front{
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        font-size: .7rem;
        left: 50%;
        transform: translateX(-50%);
        bottom: 150px;
        width: 80px;
        height: 80px;
        background: $fruit;
        cursor: pointer;
        border-radius: 50%;
        z-index: 1;

        span {
          animation: rotate 2s ease infinite;
        }

        &:before {
          content: '';
          position: absolute;
          display: block;
          width: 100%;
          height: 100%;
          background: $fruit;
          border-radius: 50%;
          z-index: -1;
          animation: pulsation 1.2s ease infinite;
        }
        &:hover {
          background: $fruit-light;
          color: $butter;
          &:before {
            background: $fruit-light;
          }
          span {
            animation: step-end;
          }
        }

      }
      .cake-box {
        /*position: absolute;*/
        /*bottom: 0;*/
        margin: 0 auto;
        width: 50%;
        color: white;

        .layer {
          position: relative;
          margin: 0 auto;
          width: 80%;
          height: 16px;
          top: 22px;

          .circle-top {
            box-sizing: border-box;
            position: absolute;
            border-bottom: $butter-black solid 3px;
            top: -6px;
            box-shadow: 0 1px 0px lighten($butter-black, 5%);
            width: 100%;
            height: 13px;
            border-radius: 50%;
            z-index: 1;
            color: black;
          }
          .mass-center {
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            color: black;
            display: flex;
            justify-content: center;
            align-items: center;

            &:before {
              padding-top: 12px;
              content: attr(data-inf);
              font-size: .72rem;
              position: absolute;
              z-index: 4;
              /*animation: opc 1s;*/
            }
          }

          .circle-bottom {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            bottom: -6px;
            width: 100%;
            height: 13px;
            border-radius: 50%;
            border-bottom: $butter solid 3px;
            z-index: 2;
          }
        }
        .plate {
          width: 100%;
          height: 30px;
          border-radius: 50%;
          margin-top: 8px;
          box-shadow: 0 2px 0 darken(#ccc, 10%),
          0 4px 0 darken(#ccc, 10%),
          0 5px 10px rgba(black, 0.5);
          background-color: #ccc;
          margin-bottom: 10px;
        }
      }
    }
    .cake-options {
      padding-top: 2rem;
      width: 60%;
      position: relative;

      .cake-options__info {
        position: absolute;
        margin-right: 2rem;
        padding: 1rem;
        background: rgba(#42b983, .2);

        .cake-options__info--header {
          display: flex;
          justify-content: space-between;
          align-items: center;

          .button-options {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
            background: none;
            outline: none;
            width: 6rem;
            height: 2rem;
            text-decoration: none;
            border: $fruit solid;
            color: $fruit;
            border-radius: 2rem;
            z-index: 3;

            &:before {
              position: absolute;
              content: '';
              display: block;
              background: $fruit;
              top: 0;
              left: 50%;
              transform: translateX(-50%);
              width: 0;
              height: 100%;
              opacity: 0;
              transition: width .3s, color .3s, opacity .3s;
              z-index: 0;

            }
            &:hover {
              color: white;
              &:before {
                width: 110%;
                height: 110%;
                z-index: -1;
                opacity: 1;


              }
            }

          }
        }



        ol{
          list-style: none;
          counter-reset: item;
          margin-left: -10;

          li {

            counter-increment: item;
            margin-bottom: 5px;
            &::before {

              margin-right: 10px;
              content: counter(item);
              background: lightblue;
              border-radius: 100%;
              color: white;
              width: 1.2em;
              text-align: center;
              display: inline-block;
            }
          }

        }
      }

      .cake-options__table {
        margin-right: 2rem;

        .cake-options__header {
          display: flex;
          justify-content: space-between;
          align-items: center;

          .price-sum {

            padding: .2rem .5rem;
            border-radius: .4rem;
            color: green;
            border: 1px solid red;
          }
          .addLayers-button {
            background: green;
            color: white;
            font-weight: 600;
            transition: color .2s, background-color .2s;
            animation: scale_puls .4s 1s;

            &:hover {
              transform: scale(1.1);

            }

          }
          .reset-button {
            color: white;
            background: red;
            transition: color .2s, background-color .2s;
            font-weight: 600;

            &:hover {
              color: red;
              background: #ffccce;

            }
            &:active {
              color: white;
              background: #a48183;

            }
          }
        }
        .table-options {
          width: 100%;
          border: none;
          border-collapse: collapse;

          thead {
            padding: .6rem;
            /*border: .2rem solid red;*/
            background: #525252;
            color: white;
            border: none;


            th {
              padding: .6rem .2rem;
              border: none;
              &:first-child {
                width: 5%;
              }
              /*color: blue;*/
              width: 20%;
            }
          }
          tbody {
            background: rgba(226, 226, 226, 0.13);
            td {
              .button-delete {
                display: inline-block;
                height: 1.4rem;
                width: 1.4rem;
                cursor: pointer;
                position: relative;
                transition: transfrom .2s;


                &::before {
                  content: '';
                  display: block;
                  position: absolute;
                  border-radius: .2rem;
                  top: 50%;
                  left: 0;
                  width: 100%;
                  height: .4rem;
                  background: #525252;
                  transform: translateY(-50%) rotate(-45deg);

                }
                &::after {
                  content: '';
                  display: block;
                  position: absolute;
                  border-radius: .2rem;
                  bottom: 50%;
                  right: 0;
                  width: 100%;
                  height: .4rem;
                  background: #525252;
                  transform: translateY(50%) rotate(45deg);

                }
                &:hover {
                  transition: background-color 1s;

                  &:before {
                    transform:  rotate(180deg) translateY(50%);
                    background: red;
                  }
                  &:after {
                    background: red;
                    transform:  rotate(180deg) translateY(-50%);
                  }
                }
              }
              .option-min-add {
                display: flex;
                justify-content: center;
                align-items: center;
                .option-min {
                  display: block;
                  position: relative;
                  width: 1rem;
                  height: 1rem;
                  cursor: pointer;
                  margin-right: .3rem;

                  &:before {
                    position: absolute;
                    display: block;
                    content: '';
                    width: 100%;
                    height: .4rem;
                    top: 50%;
                    left: 0;
                    background: #525252;
                    transform: translateY(-50%);
                  }
                  &:hover {
                    &:before {
                      background: #828282;
                    }

                  }
                }
                .option-add {
                  position: relative;
                  width: 1rem;
                  height: 1rem;
                  cursor: pointer;
                  margin-left: .3rem;

                  &:before, &:after {
                    position: absolute;
                    display: block;
                    content: '';
                    width: 100%;
                    height: .4rem;
                    top: 50%;
                    left: 0;
                    background: #525252;
                    transform: translateY(-50%);
                  }
                  &:after {
                    bottom: 0;
                    transform: translateY(-50%) rotate(90deg) ;
                  }
                  &:hover {
                    &:before, &:after {
                      background: #828282;
                    }

                  }

                }

              }

              padding: .2rem 0;
              text-align: center;
              border-bottom: .12rem solid rgba(179, 179, 179, 0.13);
            }
          }
        }

      }
    }
  }
  @media screen and (max-width: 940px) {
    .cake {
      width: 100%;
      .cake-view{
        display: none;
      }
      .cake-options {
        width: 100%;
        margin: 0;
        box-sizing: border-box;
        .cake-options__info {
          width: 100%;
        }
        .cake-options__table {
          margin: 0;
        }
      }
    }

  }
  @media screen and (max-width: 540px) {
    .cake {
      .cake-options {
        width: 100%;
        min-width: 338px;
        .cake-options__table {
          .cake-options__header {
            padding: 1rem 0;
            h2 {display: none;}
          }
        }
      }
    }

  }

</style>




